import React from 'react'
import {Link,graphql } from "gatsby"

import PropTypes from "prop-types"
import BreadcumHeader from "../components/breadcumHeader"
import Footer from "../components/footer"
import Clients from "../components/clients"
import Nav from "../components/nav"
import Axios from "../../axios"
import { BASE_URL } from "../../config"
import ReactMarkdown from "react-markdown"

export const query = graphql`
  query($slug: String!) {
    allStrapiProductTitles(filter: {slug: {eq: $slug}}) {
        nodes {
          name
          slug
          description
          product_items {
            name
            description
            id
            img {
              localFile {
                childImageSharp {
                  original {
                    src
                  }
                }
              }
            }
          }
          
        }
      }
}
`

function ProductDetailPage({ data }) {

    const {product_items, name, description} = data.allStrapiProductTitles.nodes[0]

    return (
        <div>
      <Nav />
      <BreadcumHeader title={"Products"} heroImageName={"product"} />
      <>
        <section className="norbar-snap-on-group section-padding">
         
          <div className="container norbar-snap-container mt-3">
          <h1 className="h-about-us_norbar mt-4">{name}</h1>
          <div className="hr">
            <hr className="hr-msar-dark hr-product-page" style={{width:`${name?.length * 18}px`}} />
          </div>
            <div className="row">
              <div className="mnt-3 col-sm-12">
              <div className="main-anchor">
                <ReactMarkdown
                  source={description}
                  transformImageUri={uri =>
                    uri.startsWith("http") ? uri : `${BASE_URL}${uri}`
                  }
                />
                </div>
              </div>
            </div>
          </div>
        </section>

        <section className="norbar-snap-on-group section-padding">
          <div className="container norbar-snap-container">
            <h1 className="h-about-us_norbar">{name} Products</h1>

            {product_items?.map((document, i) => (
              <>
                {i % 2 === 0 ? (
                  <div className="row">
                    <div className="col-md-4 order-2 order-md-1">
                      <img
                        className="NorbarProducts_Image"
                        src={document.img.localFile.childImageSharp?.original.src}
                        alt={'img'}
                        width={350}
                        height={300}
                      />

                    </div>
                    <div className="col-md-8 order-1 order-md-2">
                      <h4 className="TorqueWrenches-p_heading">
                        {document?.name}
                      </h4>
                      <div className="main-anchor">
                      <ReactMarkdown
                        source={document?.description}
                        transformImageUri={uri =>
                          uri.startsWith("http") ? uri : `${BASE_URL}${uri}`
                        }
                      />
                      </div>
                    </div>
                  </div>
                ) : (
                  <div className="row mt-4">
                    <div className="col-md-8">
                      <h4 className="TorqueWrenches-p_heading mt-4 mt-md-0">
                        {document?.name}
                      </h4>
                      <div className="main-anchor">
                      <ReactMarkdown
                        source={document?.description}
                        transformImageUri={uri =>
                          uri.startsWith("http") ? uri : `${BASE_URL}${uri}`
                        }
                      />
                      </div>
                    </div>
                    <div className="col-md-4">
                    <img
                        className="NorbarProducts_Image"
                        src={document.img.localFile.childImageSharp?.original.src}
                        alt={'img'}
                        width={350}
                        height={300}
                      />

                    </div>
                  </div>
                )}
              </>
            ))}
          </div>
        </section>
      </>
      <Clients />
      <Footer />
    </div>
    )
}

export default ProductDetailPage
