import React, { useState, useEffect } from "react"
import {Link,graphql, useStaticQuery,navigate } from "gatsby"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import sliderBg from "../images/home/slider-bg.png"
import { Location } from "@reach/router"
import Axios from "../../axios"
import { BASE_URL } from "../../config"


const query = graphql`
    {
      allStrapiHeroImages {
    nodes {
      about {
        localFile {
          childImageSharp {
            original {
              src
            }
          }
        }
      }
      contact {
        localFile {
          childImageSharp {
            original {
              src
            }
          }
        }
      }
      home {
        localFile {
          childImageSharp {
            original {
              src
            }
          }
        }
      }
      product {
        localFile {
          childImageSharp {
            original {
              src
            }
          }
        }
      }
      service {
        localFile {
          childImageSharp {
            original {
              src
            }
          }
        }
      }
    }
  }

    }
`

function BreadcumHeader(props) {

  const data = useStaticQuery(query)
  const heroImages = data.allStrapiHeroImages.nodes[0]

  // const [HeroImages, setHeroImages] = useState({})
  // useEffect(() => {
  //   async function fetchData() {
  //     const request = await Axios.get("/hero-images")
  //     setHeroImages(request.data)
  //     return request
  //   }
  //   fetchData()
  // }, [])

  const { title, heroImageName } = props

  return (
    <section id="breadcrumb">
      <section
        className="container-fluid msar-breadcrumb-bg"
        // style={{
        //   backgroundImage: `url(${BASE_URL}${
        //     HeroImages["" + heroImageName + ""]
        //       ? HeroImages["" + heroImageName + ""][0]?.formats?.medium?.url
        //       : ""
        //   })`,
        // }}

        style={{
          backgroundImage: `url(${heroImages[heroImageName][0].localFile.childImageSharp?.original.src})`
        }}
      >
        <section className="row">
          <section className="container">
            <section className="row m-1 m-md-0">
              <section className="col-md-12">
                <section className="service-breadcrumb text-center text-white m-5 p-0 p-md-8">
                  <h1 className="font-weight-bold">{title}</h1>
                  <h6 className="font-weight-light">
                    {" "}
                    <Link style={{ color: "white" }} to="/">
                      Home
                    </Link>{" "}
                    &gt; {title}{" "}
                  </h6>
                </section>
              </section>
            </section>
          </section>
        </section>
      </section>
    </section>
  )
}

export default BreadcumHeader
